import React, { useState, useEffect } from 'react';
import { Button, Drawer, Avatar, Typography, Descriptions, Card, message, Modal } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import { doc, updateDoc } from 'firebase/firestore';
import { db } from '../../../config';
import { toast } from 'react-toastify';

const { Title, Text } = Typography;

const Ungvien = ({ cv, onUpdate }) => {
    const [visible, setVisible] = useState(false);
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [isRejectModalVisible, setIsRejectModalVisible] = useState(false);
    const [isApproved, setIsApproved] = useState(cv.PheDuyet);
    const [isRejected, setIsRejected] = useState(cv.TuChoi);
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        setIsApproved(cv.PheDuyet);
        setIsRejected(cv.TuChoi);
    }, [cv.PheDuyet, cv.TuChoi]);

    const showDrawer = () => setVisible(true);
    const onClose = () => setVisible(false);

    const showModal = () => setIsModalVisible(true);
    const handleCancel = () => setIsModalVisible(false);

    const showRejectModal = () => setIsRejectModalVisible(true);
    const handleRejectCancel = () => setIsRejectModalVisible(false);

    const handleApproveCV = async () => {
        setIsLoading(true);
        try {
            const cvRef = doc(db, 'CV', cv.id);
            await updateDoc(cvRef, {
                PheDuyet: true,
                TuChoi: false
            });
            setIsApproved(true);
            setIsRejected(false);
            toast.success('Hồ sơ đã được phê duyệt.', {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            if (onUpdate) {
                onUpdate(cv.id, true, false);
            }
        } catch (error) {
            console.error("Error approving CV: ", error);
            message.error('Có lỗi xảy ra khi phê duyệt hồ sơ');
        } finally {
            setIsLoading(false);
            setIsModalVisible(false);
        }
    };

    const handleRejectCV = async () => {
        setIsLoading(true);
        try {
            const cvRef = doc(db, 'CV', cv.id);
            await updateDoc(cvRef, {
                PheDuyet: false,
                TuChoi: true
            });
            setIsApproved(false);
            setIsRejected(true);
            toast.success('Hồ sơ đã bị từ chối.', {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            if (onUpdate) {
                onUpdate(cv.id, false, true);
            }
        } catch (error) {
            console.error("Error rejecting CV: ", error);
            message.error('Có lỗi xảy ra khi từ chối hồ sơ');
        } finally {
            setIsLoading(false);
            setIsRejectModalVisible(false);
        }
    };

    return (
        <>
            <Button type="primary" onClick={showDrawer}>
                Xem hồ sơ ứng viên
            </Button>
            <Drawer
                title={`Hồ sơ ứng viên: ${cv.HoTen}`}
                placement="right"
                onClose={onClose}
                open={visible}
                width={720}
            >
                <div style={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
                    <div style={{ display: 'flex', alignItems: 'center', gap: '16px' }}>
                        <Avatar
                            size={64}
                            src={cv.Hinhdaidien}
                            icon={!cv.Hinhdaidien && <UserOutlined />}
                        />
                        <div>
                            <Title level={2} style={{ marginBottom: 5 }}>{cv.HoTen}</Title>
                            <Text>{cv.Vitriungtuyen}</Text>
                        </div>
                    </div>
                    <div class="apply-form__group-body--line"></div>
                    <Descriptions title="Thông tin cá nhân" column={window.innerWidth < 768 ? 1 : 2}>
                        <Descriptions.Item label="Email">{cv.Email}</Descriptions.Item>
                        <Descriptions.Item label="Số điện thoại">{cv.SoDienThoai}</Descriptions.Item>
                        <Descriptions.Item label="Ngày sinh">{cv.Ngaysinh}</Descriptions.Item>
                        <Descriptions.Item label="CMND/CCCD">{cv.CMND}</Descriptions.Item>
                        <Descriptions.Item label="Giới tính">{cv.GioiTinh}</Descriptions.Item>
                        <Descriptions.Item label="Chiều cao">{cv.ChieuCao} cm</Descriptions.Item>
                        <Descriptions.Item label="Cân nặng">{cv.CanNang} kg</Descriptions.Item>
                    </Descriptions>

                    <Descriptions title="Thông tin chung" column={1}>
                        <Descriptions.Item label="Địa chỉ">
                            {`${cv.Diachi}, ${cv.Phuongxa}, ${cv.Quanhuyen}, ${cv.City}`}
                        </Descriptions.Item>
                        <Descriptions.Item label="Thu nhập mong muốn">{cv.Thunhapmongmuon}</Descriptions.Item>
                        <Descriptions.Item label="Kinh nghiệm">{cv.Kinhnghiem}</Descriptions.Item>
                        <Descriptions.Item label="Ngày gửi CV">{cv.NgayGuiCV}</Descriptions.Item>
                    </Descriptions>
                    <div class="apply-form__group-body--line"></div>
                    <div>
                        {cv.KinhNghiemLamViec && Object.entries(cv.KinhNghiemLamViec).map(([key, exp]) => (
                            <>
                                <Title level={3}>Kinh nghiệm làm việc</Title>
                                <Card key={key} style={{ marginBottom: '16px' }}>
                                    <Card.Meta
                                        title={exp.ViTriDamNhiem}
                                        description={exp.CongTyDoanhNghiep}
                                    />
                                    <Text type="secondary">
                                        {exp.TuNgay} - {exp.DangLamViec ? 'Hiện tại' : exp.DenNgay}
                                    </Text>
                                    <p>{exp.MoTaCongViec}</p>
                                </Card>
                                <div class="apply-form__group-body--line"></div>
                            </>
                        ))}
                    </div>

                    <Descriptions title="Thông tin khác" column={window.innerWidth < 768 ? 1 : 2}>
                        <Descriptions.Item label="Thu nhập mong muốn" labelStyle={{ width: '200px' }}>
                            {cv.Thunhapmongmuon}
                        </Descriptions.Item>
                        <Descriptions.Item label="Bạn có sẵn sàng làm việc xoay ca không? (Ca sáng, ca tối)" labelStyle={{ width: '200px' }}>
                            {cv.Cauhoi1}
                        </Descriptions.Item>
                        <Descriptions.Item label="Bạn có thể làm việc vào các ngày Lễ/Tết không?" labelStyle={{ width: '200px' }}>
                            {cv.Cauhoi2}
                        </Descriptions.Item>
                        <Descriptions.Item label="Bạn có sẵn sàng làm việc cuối tuần không?" labelStyle={{ width: '200px' }}>
                            {cv.Cauhoi3}
                        </Descriptions.Item>
                        <Descriptions.Item label="Bạn có sẵn sàng làm việc ca tối không?" labelStyle={{ width: '200px' }}>
                            {cv.Cauhoi4}
                        </Descriptions.Item>
                    </Descriptions>
                </div>
                <div class="apply-form__group-body--line"></div>
                <div style={{ display: 'flex', gap: '16px', marginTop: '16px' }}>
                    <Button
                        type="primary"
                        onClick={showModal}
                        disabled={isApproved}
                    >
                        {isApproved ? 'Đã phê duyệt' : 'Phê duyệt hồ sơ'}
                    </Button>
                    <Button
                        type="default"
                        onClick={showRejectModal}
                        disabled={isRejected}
                    >
                        {isRejected ? 'Đã từ chối' : 'Từ chối hồ sơ'}
                    </Button>
                </div>
            </Drawer>
            <Modal
                title="Xác nhận phê duyệt"
                open={isModalVisible}
                onOk={handleApproveCV}
                onCancel={handleCancel}
                okText="Phê duyệt"
                cancelText="Hủy"
                confirmLoading={isLoading}
            >
                <p>Bạn có chắc chắn muốn phê duyệt hồ sơ này không?</p>
            </Modal>
            <Modal
                title="Xác nhận từ chối"
                open={isRejectModalVisible}
                onOk={handleRejectCV}
                onCancel={handleRejectCancel}
                okText="Từ chối"
                cancelText="Hủy"
                confirmLoading={isLoading}
            >
                <p>Bạn có chắc chắn muốn từ chối hồ sơ này không?</p>
            </Modal>
        </>
    );
};

export default Ungvien;
